import "./ContactListAdmin.css";
import { DataGrid } from "@mui/x-data-grid";
import { DeleteOutline } from "@mui/icons-material";
import { Link } from "react-router-dom";
import { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { deleteContact, getContacts } from "../../../redux/contact/apiCalls";

export default function ContactListAdmin() {
    const dispatch = useDispatch();
    const { contacts } = useSelector((state) => state.contacts);
    const { currentUser } = useSelector((state) => state.user);

    useEffect(() => {
        getContacts(dispatch)
    }, [dispatch])

    const handleDelete = (id) => {
        deleteContact(dispatch, id, currentUser);
    };

    const columns = [
        { field: "_id", headerName: "ID", width: 220 },
        {
            field: "product",
            headerName: "Contact",
            width: 200,
            renderCell: (params) => {
                return (
                    <div className="contactListItem">
                        <img className="contactListImg" src={params.row.img} alt="" />
                        {params.row.title}
                    </div>
                );
            },
        },
        { field: "inStock", headerName: "Stock", width: 200 },
        {
            field: "price",
            headerName: "Price",
            width: 160,
        },
        {
            field: "action",
            headerName: "Action",
            width: 150,
            renderCell: (params) => {
                return (
                    <>
                        <Link to={"/admin/contact/" + params.row._id}>
                            <button className="contactListEdit">Edit</button>
                        </Link>
                        <DeleteOutline
                            className="contactListDelete"
                            onClick={() => handleDelete(params.row._id)}
                        />
                    </>
                );
            },
        },
    ];
    return (
        <div className="contactList">
            <DataGrid
                rows={contacts}
                disableSelectionOnClick
                columns={columns}
                getRowId={(row) => row._id}
                pageSize={8}
                checkboxSelection
            />
        </div>
    );
}
