import { useState } from "react";
import { useDispatch } from "react-redux";
import { useNavigate } from "react-router-dom";
import styled from "styled-components";
import { signup } from "../redux/user/apiCalls";
import { mobile } from "../responsive";

const Container = styled.div`
  width: 100vw;
  height: 100vh;
  background: linear-gradient(
      rgba(255, 255, 255, 0.5),
      rgba(255, 255, 255, 0.5)
    ),
    url("https://images.pexels.com/photos/6984661/pexels-photo-6984661.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=650&w=940")
      center;
  background-size: cover;
  display: flex;
  align-items: center;
  justify-content: center;
`;

const Wrapper = styled.div`
  width: 40%;
  padding: 20px;
  background-color: white;
  ${mobile({ width: "75%" })}
`;

const Title = styled.h1`
  font-size: 24px;
  font-weight: 300;
`;

const Form = styled.form`
  display: flex;
  flex-wrap: wrap;
`;

const Input = styled.input`
  flex: 1;
  min-width: 40%;
  margin: 20px 10px 0px 0px;
  padding: 10px;
`;

const Agreement = styled.span`
  font-size: 12px;
  margin: 20px 0px;
`;

const Button = styled.button`
  width: 40%;
  border: none;
  padding: 15px 20px;
  background-color: teal;
  color: white;
  cursor: pointer;
`;

const Register = () => {
  const [form, setForm] = useState({})
  const dispatch = useDispatch()
  const navigate = useNavigate()

  // Constants
  const NAME = 'name'
  const LAST_NAME = 'lastName'
  const USERNAME = 'username'
  const EMAIL = 'email'
  const PASSWORD = 'password'
  const CONFIRM_PASSWORD = 'confirmPassword'

  const handleChange = ({ target }) => {
    const { name, value } = target
    setForm((prevState) => ({ ...prevState, [name]: value }))
  }

  const onSubmit = (ev) => {
    ev.preventDefault()
    validateForm()
  }

  const validateForm = async () => {
    if (!form.hasOwnProperty(NAME)
      || !form.hasOwnProperty(LAST_NAME)
      || !form.hasOwnProperty(USERNAME)
      || !form.hasOwnProperty(EMAIL)
      || !form.hasOwnProperty(PASSWORD)) return alert('Please fill all the fields')

    else if (form[CONFIRM_PASSWORD] !== form[PASSWORD]) alert('Passwords do not match')
    else {
      const user = await signup(dispatch, form)
      if (user) navigate('/')
    }
  }

  return (
    <Container>
      <Wrapper>
        <Title>CREATE AN ACCOUNT</Title>
        <Form onSubmit={onSubmit}>
          <Input placeholder="name" name={NAME} onChange={handleChange} />
          <Input placeholder="last name" name={LAST_NAME} onChange={handleChange} />
          <Input placeholder="username" name={USERNAME} onChange={handleChange} />
          <Input placeholder="email" name={EMAIL} onChange={handleChange} />
          <Input placeholder="password" name={PASSWORD} onChange={handleChange} />
          <Input placeholder="confirm password" name={CONFIRM_PASSWORD} onChange={handleChange} />
          <Agreement>
            By creating an account, I consent to the processing of my personal
            data in accordance with the <b>PRIVACY POLICY</b>
          </Agreement>
          <Button>CREATE</Button>
        </Form>
      </Wrapper>
    </Container>
  );
};

export default Register;
