import "./userList.css";
import { DataGrid } from "@mui/x-data-grid";
import { DeleteOutline } from "@mui/icons-material";
import { Link } from "react-router-dom";
import { useEffect, useState } from "react";
import { getUsers, removeUser } from "../../../redux/user/apiCalls";
import { useDispatch, useSelector } from "react-redux";

export default function UserList() {
  const { users, currentUser } = useSelector((state) => state.user);
  const [data, setData] = useState(null);
  const dispatch = useDispatch()

  useEffect(() => {
    if (!users.length && currentUser) getUsers(dispatch, currentUser);
  }, [currentUser]);

  useEffect(() => {
    loadUsers()
  }, [users])

  const loadUsers = () => {
    if (!users) return
    const data = users.map(user => ({ ...user, id: user._id }))
    setData(data)
  }

  const handleDelete = (id) => {
    removeUser(dispatch, id, currentUser)
  };

  const columns = [
    { field: "id", headerName: "ID", width: 90 },
    {
      field: "user",
      headerName: "User",
      width: 200,
      renderCell: (params) => {
        return (
          <div className="userListUser">
            <img className="userListImg" src={params.row.avatar} alt="" />
            {params.row.username}
          </div>
        );
      },
    },
    { field: "email", headerName: "Email", width: 200 },
    {
      field: "isAdmin",
      headerName: "Admin Status",
      width: 120,
    },
    {
      field: "transaction",
      headerName: "Transaction Volume",
      width: 160,
    },
    {
      field: "action",
      headerName: "Action",
      width: 150,
      renderCell: (params) => {
        return (
          <>
            <Link to={"/admin/user/" + params.row.id}>
              <button className="userListEdit">Edit</button>
            </Link>
            <DeleteOutline
              className="userListDelete"
              onClick={() => handleDelete(params.row.id)}
            />
          </>
        );
      },
    },
  ];

  if (!data) return <div>Loading...</div>
  return (
    <div className="userList">
      <DataGrid
        rows={data}
        disableSelectionOnClick
        columns={columns}
        pageSize={8}
        checkboxSelection
      />
    </div>
  );
}
