import "./sidebar.css";
import { LineStyle, PermIdentity, Storefront, } from "@mui/icons-material";
import { NavLink } from "react-router-dom";
import { useEffect, useState } from "react";

export default function Sidebar() {

  const links = [
    { title: 'Dashboard', list: [{ text: 'Home', Icon: LineStyle, link: '/admin' }] },
    { title: 'Quick Menu', list: [{ text: 'Users', Icon: PermIdentity, link: '/admin/users' }] },
    { title: 'Notifications', list: [{ text: 'Contacts', Icon: Storefront, link: '/admin/contacts' }] },
  ]


  return (
    <div className="sidebar">
      <div className="sidebarWrapper">
        {links.map((link, idx) => {
          return <LinkList key={idx} link={link} />
        })}
      </div>
    </div>
  );
}


const LinkList = (props) => {
  const { link } = props

  return (
    <div className="sidebarMenu">
      <h3 className="sidebarTitle">{link.title}</h3>
      {link.list.map((item, idx) => <LinkPreview key={idx} item={item} />)}
    </div>
  )
}


const LinkPreview = ({ item }) => {
  const { link, Icon, text } = item
  const [isActive, setIsActive] = useState(false)

  useEffect(() => {
    isPathNameExact()
  }, [])

  const isPathNameExact = () => {
    const isActive = window.location.pathname === link
    setIsActive(isActive)
  }

  const handleChange = () => {
    if (isActive) setIsActive(false)
    else setIsActive(true)
  }

  return (
    <NavLink to={link} className="list">
      <li className={`sidebarListItem ${isActive ? 'active' : ''}`} onChange={handleChange}>
        <Icon className="sidebarIcon" />
        {text}
      </li>
    </NavLink>
  )
}


