import "./ContactAdmin.css"
import { Link, useParams } from "react-router-dom"
import { Publish } from "@mui/icons-material"
import { useDispatch, useSelector } from "react-redux"
import { useEffect, useMemo, useState } from "react"
import Chart from "../../../components/admin/chart/Chart"
import { updateContact } from "../../../redux/contact/apiCalls"
// import { userRequest } from "../../requestMethods"

export default function ContactAdmin() {
  const { contacts } = useSelector((state) => state.contacts)
  const { currentUser } = useSelector((state) => state.user)

  const { contactId } = useParams()
  const dispatch = useDispatch()

  const [pStats, setPStats] = useState([])
  const [contact, setContact] = useState(null)
  const [form, setForm] = useState({
    _id: contactId || '',
    title: '',
    desc: '',
    price: '',
    inStock: '',
  })


  const MONTHS = useMemo(() => ["Jan", "Feb", "Mar", "Apr", "May", "Jun", "Jul", "Agu", "Sep", "Oct", "Nov", "Dec"], []);

  useEffect(() => {
    // const getStats = async () => {
    //   try {
    //     const res = await userRequest.get("orders/income?pid=" + contactId);
    //     const list = res.data.sort((a, b) => {
    //       return a._id - b._id
    //     })
    //     list.map((item) =>
    //       setPStats((prev) => [
    //         ...prev,
    //         { name: MONTHS[item._id - 1], Sales: item.total },
    //       ])
    //     );
    //   } catch (err) {
    //     console.log(err);
    //   }
    // };
    // getStats();
  }, [contactId, MONTHS]);

  useEffect(() => {
    const contact = contacts.find(currContact => currContact._id === contactId)
    setContact(contact)
  }, [contactId, contacts])

  useEffect(() => {
    if (contact) updateForm()
  }, [contact])

  const updateForm = () => {
    setForm((prevState) => (
      {
        ...prevState,
        title: contact.title || '',
        desc: contact.desc || '',
        price: contact.price || '',
        inStock: contact.inStock || 'false',
      }
    ))
  }

  const handleChange = ({ target }) => {
    const { value, name } = target
    setForm((prevState) => ({ ...prevState, [name]: value }))
  }

  const onSaveContact = (ev) => {
    ev.preventDefault()
    updateContact(dispatch, form, currentUser)
  }

  if (!contact) return <div>Loading...</div>
  return (
    <div className="contact">
      <div className="contactTitleContainer">
        <h1 className="contactTitle">Contact</h1>
        <Link to="/newcontact">
          <button className="contactAddButton">Create</button>
        </Link>
      </div>
      <div className="contactTop">
        <div className="contactTopLeft">
          <Chart data={pStats} dataKey="Sales" title="Sales Performance" />
        </div>
        <div className="contactTopRight">
          <div className="contactInfoTop">
            <img src={contact.img} alt="" className="contactInfoImg" />
            <span className="contactName">{contact.title}</span>
          </div>
          <div className="contactInfoBottom">
            <div className="contactInfoItem">
              <span className="contactInfoKey">id:</span>
              <span className="contactInfoValue">{contact._id}</span>
            </div>
            <div className="contactInfoItem">
              <span className="contactInfoKey">sales:</span>
              <span className="contactInfoValue">5123</span>
            </div>
            <div className="contactInfoItem">
              <span className="contactInfoKey">in stock:</span>
              <span className="contactInfoValue">{contact.inStock}</span>
            </div>
          </div>
        </div>
      </div>
      <div className="contactBottom">
        <form className="contactForm" onSubmit={onSaveContact}>
          <div className="contactFormLeft">
            <label>Contact Name</label>
            <input type="text" placeholder={contact.title} name="title" onChange={handleChange} />
            <label>Contact Description</label>
            <input type="text" placeholder={contact.desc} name="desc" onChange={handleChange} />
            <label>Price</label>
            <input type="text" placeholder={contact.price} name="price" onChange={handleChange} />
            <label>In Stock</label>
            <select name="inStock" id="idStock" onChange={handleChange} defaultValue={contact.inStock}>
              <option value="true">Yes</option>
              <option value="false">No</option>
            </select>
          </div>
          <div className="contactFormRight">
            <div className="contactUpload">
              <img src={contact.img} alt="" className="contactUploadImg" />
              <label htmlFor="file">
                <Publish />
              </label>
              <input type="file" id="file" style={{ display: "none" }} />
            </div>
            <button className="contactButton">Update</button>
          </div>
        </form>
      </div>
    </div>
  );
}
