import Chart from "../../../components/admin/chart/Chart";
import FeaturedInfo from "../../../components/admin/featuredInfo/FeaturedInfo";
import "./home.css";
import WidgetSm from "../../../components/admin/widgetSm/WidgetSm";
import WidgetLg from "../../../components/admin/widgetLg/WidgetLg";
import { useEffect, useMemo, useState } from "react";
import { getUsersStats } from "../../../redux/user/apiCalls";
import { useDispatch, useSelector } from "react-redux";

export default function HomeAdmin() {
    const { currentUser } = useSelector(state => state.user)
    const [userStats, setUserStats] = useState([]);
    const dispatch = useDispatch()

    const MONTHS = useMemo(
        () => [
            "Jan",
            "Feb",
            "Mar",
            "Apr",
            "May",
            "Jun",
            "Jul",
            "Agu",
            "Sep",
            "Oct",
            "Nov",
            "Dec",
        ],
        []
    );

    useEffect(() => {
        loadStats()
    }, [MONTHS]);

    const loadStats = () => {
        getUsersStats(dispatch, currentUser)
    }

    return (
        <div className="home">
            <FeaturedInfo />
            <Chart
                data={userStats}
                title="User Analytics"
                grid
                dataKey="Active User"
            />
            <div className="homeWidgets">
                <WidgetSm />
                <WidgetLg />
            </div>
        </div>
    );
}
