import { deleteContactFailure, deleteContactStart, deleteContactSuccess, getContactFailure, getContactStart, getContactSuccess, updateContactFailure, updateContactStart, updateContactSuccess } from "./contactRedux";
import { contactService } from '../../services/contact.service'

export const getContacts = async (dispatch) => {
    dispatch(getContactStart());
    try {
        const contacts = await contactService.query()
        dispatch(getContactSuccess(contacts));
    } catch (err) {
        console.log('Error => ', err)
        dispatch(getContactFailure());
    }
}

export const deleteContact = async (dispatch, id, currentUser) => {
    dispatch(deleteContactStart());
    try {
        await contactService.removeContact(id, currentUser)
        dispatch(deleteContactSuccess(id))
    } catch (err) {
        console.log('Error => ', err)
        dispatch(deleteContactFailure());
    }
};

export const updateContact = async (dispatch, contact, currentUser) => {
    dispatch(updateContactStart());
    try {
        const res = await contactService.updateContact(contact, currentUser)
        dispatch(updateContactSuccess(res))
    } catch (err) {
        console.log('Error => ', err)
        dispatch(updateContactFailure());
    }
};

